*, :before, :after {
  box-sizing: border-box;
}

:root {
  --color-text: #000;
  --color-header: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
}

body {
  margin: 0;
}

a {
  display: inline-block;
}

img {
  display: block;
}

.nav__link {
  color: var(--color-text);
  text-transform: uppercase;
  text-decoration: none;
  overflow-x: hidden;
}

.nav__item {
  height: 60px;
  line-height: 60px;
  list-style: none;
  position: relative;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link.is-active, .nav__link:hover {
  color: var(--color-header);
}

.is-active:after {
  content: "";
  background-color: var(--color-header);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
